<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="register" ref="registerForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="register.id"/>
        </el-form-item>
        <el-form-item label="" prop="photo">
          <sui-photo type="register_photo" v-model="register.photo" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="loginName">
          <el-input v-model="register.loginName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="userName">
          <el-input v-model="register.userName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="register.phone" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="register.email" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "registerForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        register: {
          photo: "",
          loginName: "",
          password: "",
          userName: "",
          sex: "",
          userNo: "",
          phone: "",
          idCard: "",
          email: "",
          userType: "",
          userAuthorId: "",
          address: ""
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'},{validator: this.$validator.unique, form: this, tableName: 'register', column: 'login_name', model: 'register', message: '', trigger: 'blur'}],
          email: [{type: 'email', message: '', trigger:'blur'},{validator:this.$validator.unique,form:this,tableName:'register',column:'email',model:'register',message: '',trigger: 'blur'}],
          phone: [{validator: this.$validator.phone, message: '11', trigger: 'blur'},{validator: this.$validator.unique, form: this,model: 'register', tableName: 'register', column: 'phone',  message: '', trigger: 'blur'}],
          idCard: [{validator:this.$validator.idCard, message: '', trigger: 'blur'},{validator: this.$validator.unique, form: this,model: 'register', tableName: 'register', column: 'id_card',  message: '', trigger: 'blur'}],
          userName: [{required: true, message: '', trigger: 'blur'}],
          sex: [{required: true, message: '', trigger: 'blur'}],
          userNo: [{required: true, message: '', trigger: 'blur'}],
          userType: [{required: true, message: '', trigger: 'blur'}],
          address: [{required: false, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      sexDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
      //
      userTypeDict() {
        return this.$store.getters.getDictArray('user_type');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getRegister(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getRegister(id) {
        this.$request.doGet("/admin/register/get?id=" + id).then(res => {
          if (res.isOk) {
            this.register = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.registerForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/register/sub', that.register).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.registerForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



