<!--  -->
<template>
  <div class="body">
    <!--  -->
    <song-lyric ref="lyricDialog" @reloadList="getList"/>
    <song-form ref="songDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="90px">
      <el-form-item label="" prop="name">
        <el-input v-model="queryForm.name" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="singerId">
        <el-select v-model="queryForm.singerId" placeholder="" size="small" filterable clearable>
          <el-option v-for="singer in singerDict" :key="singer.id" :value="singer.id" :label="singer.name"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['song:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['song:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#f4f4f5'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="picture" sortable>
        <template slot-scope="scope">
          <sui-image :src="scope.row.picture"></sui-image>
        </template>
      </el-table-column>
      <el-table-column label="" prop="name" sortable/>
      <el-table-column label="" prop="singerId" sortable>
        <template slot-scope="scope">
          <span v-for="singer in singerDict" v-if="singer.id==scope.row.singerId">{{singer.name}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="400" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['song:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['song:edit']"></el-button>
          <el-button size="mini" type="success" plain icon="el-icon-view" @click="lyricDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['song:view']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)" v-hasPermi="['song:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import songForm from './songForm'
  import songLyric from './songLyric'
    export default {
      components: {songForm,songLyric},
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          singerDict: [],
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            picture: "",
            name: "",
            singerId: "",
            lyric: "",
            introduction: "",
          }
        };
      },
      mounted() {
        //
        this.getList();
        //
        this.getSingerDict();
      },
      computed: {
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doGet('/admin/song/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //
        getSingerDict() {
          this.$request.doGet('/admin/singer/getList').then(res => {
            if (res.isOk) {
              this.singerDict = res.list;
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
          this.handleQuery();
        },
        //
        openDialog(option) {
          this.$refs.songDialog.open(option);
        },
        //
        lyricDialog(option) {
          this.$refs.lyricDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              let ids = that.selectedData.map(function (v) {return v.id});
              that.$request.doGet('/admin/song/delAll', {ids: ids}).then(res => {
                if (res.isOk) {
                  that.showSuccess("");
                  that.getList();
                }
              });
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.$request.doGet('/admin/song/delete?id=' + id).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              }
            });
          });
        }
      }
    };
</script>



