<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="author" ref="authorForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="author.id"/>
        </el-form-item>
        <el-form-item label="" prop="avatarUrl">
          <sui-photo type="author_avatarUrl" v-model="author.avatarUrl" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="nickName">
          <el-input v-model="author.nickName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="country">
          <el-input v-model="author.country" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="province">
          <el-input v-model="author.province" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="city">
          <el-input v-model="author.city" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="openid">
          <el-input v-model="author.openid" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="unionid">
          <el-input v-model="author.unionid" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "authorForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        author: {
          avatarUrl: "",
          nickName: "",
          country: "",
          province: "",
          city: "",
          openid: "",
          unionid: ""
        },
        //
        rules: {
          avatarUrl: [{required: true, message: '', trigger: 'blur'}],
          nickName: [{required: true, message: '', trigger: 'blur'}],
          country: [{required: true, message: '', trigger: 'blur'}],
          province: [{required: true, message: '', trigger: 'blur'}],
          city: [{required: true, message: '', trigger: 'blur'}],
          openid: [{required: true, message: '', trigger: 'blur'}],
          unionid: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getauthor(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getauthor(id) {
        this.$request.doGet("/admin/sysAuthor/get?id=" + id).then(res => {
          if (res.isOk) {
            this.author = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.authorForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/sysAuthor/sub', that.author).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.authorForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



