<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="playList" ref="playListForm" :rules="rules" label-width="120px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="playList.id"/>
        </el-form-item>
        <el-form-item label="" prop="userId">
          <el-select v-model="playList.userId" placeholder="" filterable :disabled="dialog.disabled" class="sui-input">
            <el-option v-for="register in registerDict" :key="register.id" :value="register.id" :label="register.loginName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="songId">
          <el-select v-model="playList.songId" placeholder="" filterable :disabled="dialog.disabled" class="sui-input">
            <el-option v-for="song in songDict" :key="song.id" :value="song.id" :label="song.name"/>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "playListForm",
    data() {
      return {
        registerDict:[],
        songDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        playList: {
          id: "",
          userId: "",
          songId: "",
        },
        //
        rules: {
          userId: [{required: true, message: '', trigger: 'change'}],
          songId: [{required: true, message: '', trigger: 'change'}],
        }
      }
    },
    computed: {
    },
    methods: {
      //
      getRegisterDict() {
        this.$request.doGet('/admin/register/getList').then(res => {
          if (res.isOk) {
            this.registerDict = res.list;
          }
        })
      },
      //
      getSongDict() {
        this.$request.doGet('/admin/song/getList').then(res => {
          if (res.isOk) {
            this.songDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getRegisterDict();//
        this.getSongDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getPlayList(option.id);
        }
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.playListForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getPlayList(id) {
        this.$request.doGet("/admin/playList/get?id=" + id).then(res => {
          if (res.isOk) {
            this.playList = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.playListForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/playList/sub', that.playList).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      }

    }
  }
</script>

<style scoped>

</style>



