<!--  -->
<template>
  <div class="body">
    <!--  -->
    <playList-form ref="playListDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="90px">
      <el-form-item label="" prop="userId">
        <el-select v-model="queryForm.userId" placeholder="" size="small" filterable clearable>
          <el-option v-for="register in registerDict" :key="register.id" :value="register.id" :label="register.loginName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="songId">
        <el-select v-model="queryForm.songId" placeholder="" size="small" filterable clearable>
          <el-option v-for="song in songDict" :key="song.id" :value="song.id" :label="song.name"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['playList:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['playList:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#f4f4f5'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="userId" sortable>
        <template slot-scope="scope">
          <span v-for="register in registerDict" v-if="register.id==scope.row.userId">{{register.loginName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="songId" sortable>
        <template slot-scope="scope">
          <span v-for="song in songDict" v-if="song.id==scope.row.songId">{{song.name}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['playList:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['playList:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)" v-hasPermi="['playList:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import playListForm from './playListForm'
    export default {
      components: {playListForm},
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          registerDict: [],
          songDict: [],
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            id: "",
            userId: "",
            songId: "",
          }
        };
      },
      mounted() {
        //
        this.getList();
        //
        this.getRegisterDict();
        //
        this.getSongDict();
      },
      computed: {
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doGet('/admin/playList/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //
        getRegisterDict() {
          this.$request.doGet('/admin/register/getList').then(res => {
            if (res.isOk) {
              this.registerDict = res.list;
            }
          });
        },
        //
        getSongDict() {
          this.$request.doGet('/admin/song/getList').then(res => {
            if (res.isOk) {
              this.songDict = res.list;
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
          this.handleQuery();
        },
        //
        openDialog(option) {
          this.$refs.playListDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              let ids = that.selectedData.map(function (v) {return v.id});
              that.$request.doGet('/admin/playList/delAll', {ids: ids}).then(res => {
                if (res.isOk) {
                  that.showSuccess("");
                  that.getList();
                }
              });
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.$request.doGet('/admin/playList/delete?id=' + id).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              }
            });
          });
        }
      }
    };
</script>



