<!--  -->
<template>
  <div class="body">
    <!--  -->
    <notice-form ref="noticeDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="100px">
      <el-form-item label="" prop="title">
        <el-input v-model="queryForm.title" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="noticeTypeRange">
        <el-select v-model="queryForm.noticeTypeRange" placeholder="" size="small" multiple collapse-tags filterable clearable>
          <el-option v-for="dict in noticeTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="publishTimeRange">
        <el-date-picker v-model="queryForm.publishTimeRange" type="daterange" value-format="yyyy-MM-dd" size="small" range-separator="" start-placeholder="" end-placeholder="" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['notice:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['notice:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#f4f4f5'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="picture" sortable>
        <template slot-scope="scope">
          <sui-image :src="scope.row.picture"></sui-image>
        </template>
      </el-table-column>
      <el-table-column label="" prop="title" sortable/>
      <el-table-column label="" prop="noticeType" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in noticeTypeDict" v-if="dict.dictValue==scope.row.noticeType">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="publishTime" sortable/>
      <el-table-column label="" align="center" width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['notice:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['notice:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)" v-hasPermi="['notice:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import noticeForm from './noticeForm'
    export default {
      components: {noticeForm},
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            picture: "",
            title: "",
            noticeTypeRange: [],
            noticeType: "",
            publishTimeRange: [],
            publishTime: "",
            content: ""
          }
        };
      },
      mounted() {
        //
        this.getList();
      },
      computed: {
        //
        noticeTypeDict() {
          return this.$store.getters.getDictArray('notice_type');
        },
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doGet('/admin/notice/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
          this.handleQuery();
        },
        //
        openDialog(option) {
          this.$refs.noticeDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              let ids = that.selectedData.map(function (v) {return v.id});
              that.$request.doGet('/admin/notice/delAll', {ids: ids}).then(res => {
                if (res.isOk) {
                  that.showSuccess("");
                  that.getList();
                }
              });
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.$request.doGet('/admin/notice/delete?id=' + id).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              }
            });
          });
        }
      }
    };
</script>



