<!--  -->
<template>
  <div class="body">
    <!--  -->
    <register-form ref="registerDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="loginName">
        <el-input v-model="queryForm.loginName" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="userName">
        <el-input v-model="queryForm.userName" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="queryForm.phone" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['register:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#f4f4f5'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="photo" sortable>
        <template slot-scope="scope">
          <sui-image :src="scope.row.photo"></sui-image>
        </template>
      </el-table-column>
      <el-table-column label="" prop="loginName" sortable/>
      <el-table-column label="" prop="userName" sortable/>
      <el-table-column label="" prop="phone" sortable/>
      <el-table-column label="" prop="email" sortable/>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['register:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['register:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['register:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import registerForm from './registerForm'
  export default {
    components: {registerForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        total: 0, //
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          photo: "",
          loginName: "",
          password: "",
          userName: "",
          sex: "",
          userNo: "",
          phone: "",
          idCard: "",
          email: "",
          userType: "",
          userAuthorId: "",
          address: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
    },
    computed: {
      //
      sexDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
      //
      userTypeDict() {
        return this.$store.getters.getDictArray('user_type');
      },
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doGet('/admin/register/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        });
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
        this.handleQuery();
      },
      //
      openDialog(option) {
        this.$refs.registerDialog.open(option);
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            let ids = that.selectedData.map(function (v) {return v.id});
            that.$request.doGet('/admin/register/delAll', {ids: ids}).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              }
            });
          } else {
            that.showWarn("");
          }
        });
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.$request.doGet('/admin/register/delete?id=' + id).then(res => {
            if (res.isOk) {
              that.showSuccess("");
              that.getList();
            }
          });
        });
      }
    }
  };
</script>



