<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="song" ref="songForm" :rules="rules" label-width="120px"  style="height: auto">
          <el-input :rows="30" type="textarea" v-model="song.lyric" placeholder=""/>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "songForm",
    data() {
      return {
        singerDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        song: {
          picture: "",
          name: "",
          singerId: "",
          writeTime: "",
          url: "",
          lyric: "",
          introduction: ""
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'blur'}],
          name: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'song',tableName:'song',column:'name',message:'',trigger:'blur'}],
          singerId: [{required: true, message: '', trigger: 'blur'}],
          writeTime: [{required: true, message: '', trigger: 'blur'}],
          url: [{required: true, message: '', trigger: 'blur'}],
          lyric: [{required: true, message: '', trigger: 'blur'}],
          introduction: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
    },
    methods: {
      //
      getSingerDict() {
        this.$request.doGet('/admin/singer/getList').then(res => {
          if (res.isOk) {
            this.singerDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getSingerDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSong(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSong(id) {
        this.$request.doGet("/admin/song/get?id=" + id).then(res => {
          if (res.isOk) {
            this.song = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.songForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/song/sub', that.song).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.songForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped lang="scss">
  .input /deep/ .el-input__inner {
    height: 360px;
  }
</style>



