<!--  -->
<template>
  <div class="body">
    <el-row style="margin-top: 100px">
      <el-col span="12">
        <echarts-line id="echartsLine" title="" ref="echartsLine"></echarts-line>
      </el-col>
      <el-col span="12">
        <echarts-pie id="echartsPie" title="" ref="echartsPie"></echarts-pie>
      </el-col>
    </el-row>
    <!--<echarts-bend id="echartsBend" title="" ref="echartsBend"></echarts-bend>-->
  </div>
</template>

<script>
    export default {
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            id: "",
            name: ""
          }
        };
      },
      mounted() {
        //
        this.getList();
      },
      computed: {
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doGet('/admin/count/getCount', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              //
              const xLine = res.data.line.xData;
              const yLine = res.data.line.yData;
              this.$refs.echartsLine.drawLine(xLine,yLine);
              //
              const pie = res.data.pie.data;
              this.$refs.echartsPie.drawPie(pie);
              //
              // const xBend = res.data.bend.xData;
              // const yBend = res.data.bend.yData;
              // this.$refs.echartsBend.drawBend(xBend,yBend);
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
          this.handleQuery();
        },
        //
        openDialog(option) {
          this.$refs.countDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              let ids = that.selectedData.map(function (v) {return v.id});
              that.$request.doGet('/admin/count/delAll', {ids: ids}).then(res => {
                if (res.isOk) {
                  that.showSuccess("");
                  that.getList();
                }
              });
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.$request.doGet('/admin/count/delete?id=' + id).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              }
            });
          });
        }
      }
    };
</script>



