<!--  -->
<template>
  <div class="body">
    <!--  -->
    <sysQuartz-form ref="sysQuartzDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="jobName">
        <el-input v-model="queryForm.jobName" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="beanName">
        <el-input v-model="queryForm.beanName" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="jobType">
        <el-select v-model="queryForm.jobType" placeholder="" size="small" clearable>
          <el-option v-for="dict in jobTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="methodName">
        <el-input v-model="queryForm.methodName" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['sysQuartz:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysQuartz:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#f4f4f5'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="jobName" width="150" sortable/>
      <el-table-column label="" prop="jobType" sortable>
        <template slot-scope="scope">
          <span v-for="dict in jobTypeDict" v-if="dict.dictValue==scope.row.jobType">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="cronExpression" sortable/>
      <el-table-column label="" prop="beanName" sortable/>
      <el-table-column label="" prop="methodName" show-overflow-tooltip sortable>
        <template slot-scope="scope">
          <span>{{scope.row.methodName}}({{scope.row.params}})</span>
        </template>
      </el-table-column>
<!--      <el-table-column label="" prop="hasParams" sortable>-->
<!--        <template slot-scope="scope">-->
<!--          <span :style="dict.style" v-for="dict in hasParamsDict" v-if="dict.dictValue==scope.row.hasParams">{{dict.dictLabel}}</span>-->
<!--        </template>-->
<!--      </el-table-column>-->
<!--      <el-table-column label="" prop="params" sortable/>-->
      <el-table-column label="|" prop="isExec" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in isExecDict" v-if="dict.dictValue==scope.row.isExec">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="jobStatus" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in jobStatusDict" v-if="dict.dictValue==scope.row.jobStatus">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="lastExecTime" show-overflow-tooltip sortable/>
<!--      <el-table-column label="" prop="remarks" show-overflow-tooltip sortable/>-->
      <el-table-column label="" align="center"  width="380" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" v-if="scope.row.isExec==0" title="" type="success" icon="el-icon-caret-right" circle @click="start(scope.row)" v-hasPermi="['sysQuartz:edit']"></el-button>
          <el-button size="mini" v-if="scope.row.isExec==1" title="" type="warning" icon="el-icon-switch-button" circle @click="stop(scope.row)" v-hasPermi="['sysQuartz:edit']"></el-button>
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysQuartz:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysQuartz:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysQuartz:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysQuartzForm from './quartzForm'
  export default {
    components: {sysQuartzForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        total: 0, //
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          jobName: "",
          beanName: "",
          params: "",
          cronExpression: "",
          jobType: "",
          hasParams: "",
          methodName: "",
          jobStatus: "",
          isExec: "",
          lastExecTime: "",
          remarks: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
    },
    computed: {
      //
      jobTypeDict() {
        return this.$store.getters.getDictArray('job_type');
      },
      //
      hasParamsDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      },
      //
      jobStatusDict() {
        return this.$store.getters.getDictArray('job_status');
      },
      //|
      isExecDict() {
        return this.$store.getters.getDictArray('is_exec');
      },
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doGet('/admin/sysQuartz/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        });
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
        this.handleQuery();
      },
      //
      openDialog(option) {
        this.$refs.sysQuartzDialog.open(option);
      },
      start(row) {
       const that = this;
        that.showConfirm("?", function () {
          row.isExec = 1;
          that.$request.doPost('/admin/sysQuartz/updateSchedule', row).then(res => {
            if (res.isOk) {
              that.showSuccess("");
            }
            that.getList();
          });
        })
      },
      stop(row) {
        const that = this;
        that.showConfirm("?", function () {
          row.isExec = 0;
          that.$request.doPost('/admin/sysQuartz/updateSchedule', row).then(res => {
            if (res.isOk) {
              that.showSuccess("");
            }
            that.getList();
          });
        })
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            let ids = that.selectedData.map(function (v) {return v.id});
            that.$request.doGet('/admin/sysQuartz/delAll', {ids: ids}).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              }
            });
          } else {
            that.showWarn("");
          }
        });
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.$request.doGet('/admin/sysQuartz/delete?id=' + id).then(res => {
            if (res.isOk) {
              that.showSuccess("");
              that.getList();
            }
          });
        });
      }
    }
  };
</script>



