<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysColumn" ref="sysColumnForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="sysColumn.id"/>
        </el-form-item>
        <el-form-item label="" prop="tableName">
          <el-input v-model="sysColumn.tableName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="tableDesc">
          <el-input v-model="sysColumn.tableDesc" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="columnName">
          <el-input v-model="sysColumn.columnName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="columnDesc">
          <el-input v-model="sysColumn.columnDesc" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="inputType">
          <el-input v-model="sysColumn.inputType" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="queryType">
          <el-input v-model="sysColumn.queryType" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="dictType">
          <el-input v-model="sysColumn.dictType" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "sysColumnForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysColumn: {
          tableName: "",
          tableDesc: "",
          columnName: "",
          columnDesc: "",
          inputType: "",
          queryType: "",
          dictType: "",
          associateType: "",
          isRequired: "",
          isUnique: "",
          isPk: "",
          isShow: "",
          sort: "",
          remarks: ""
        },
        //
        rules: {
          tableName: [{required: true, message: '', trigger: 'blur'}],
          tableDesc: [{required: true, message: '', trigger: 'blur'}],
          columnName: [{required: true, message: '', trigger: 'blur'}],
          columnDesc: [{required: true, message: '', trigger: 'blur'}],
          inputType: [{required: true, message: '', trigger: 'blur'}],
          queryType: [{required: true, message: '', trigger: 'blur'}],
          dictType: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      isRequiredDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      },
      //
      isUniqueDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      },
      //
      isPkDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      },
      //
      isShowDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysColumn(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSysColumn(id) {
        this.$request.doGet("/admin/sysColumn/get?id=" + id).then(res => {
          if (res.isOk) {
            this.sysColumn = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.sysColumnForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/sysColumn/sub', that.sysColumn).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysColumnForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



