<!--  -->
<template>
  <div class="body">
    <!--  -->
    <sysMessage-form ref="sysMessageDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="type">
        <el-input v-model="queryForm.type" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="userId">
        <el-select v-model="queryForm.userId" placeholder="" size="small" filterable clearable>
          <el-option v-for="register in registerDict" :key="register.id" :value="register.id" :label="register.loginName"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysMessage:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border row-key="id" :header-cell-style="{background:'#f4f4f5'}" :tree-props="{children: 'children', hasChildren: 'hasChildren'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="type" sortable/>
      <el-table-column label="" prop="loginName" sortable/>
      <el-table-column label="" prop="content" show-overflow-tooltip sortable>
        <template slot-scope="scope">
          <span v-html="scope.row.content"></span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="createDate" sortable/>
      <el-table-column label="" align="center" width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" v-if="!scope.row.children" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysMessage:edit']"></el-button>
          <el-button size="mini" type="success" v-if="scope.row.children" plain icon="el-icon-edit" @click="reply(scope.row)"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysMessage:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysMessageForm from './messageForm'
  export default {
    components: {sysMessageForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        total: 0, //
        dataList: [],
        registerDict: [],
        sysMessage: {
          type: "",
          pid: "",
          userId: "",
          refId: "",
          content: ""
        },
        queryForm: {
          current: 1,
          size: 10,
          type: "",
          pid: "",
          userId: "",
          refId: "",
          content: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
      //
      this.getRegisterDict();
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doGet('/admin/sysMessage/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        });
      },
      //
      getRegisterDict() {
        this.$request.doGet('/admin/register/getList').then(res => {
          if (res.isOk) {
            this.registerDict = res.list;
          }
        });
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
        this.handleQuery();
      },
      //
      openDialog(option) {
        this.$refs.sysMessageDialog.open(option);
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      reply: function (row) {
        const that = this;
        that.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({value}) => {
          if(value==null){
            that.showWarn("");
            return;
          }
          that.sysMessage.type = "";
          that.sysMessage.pid = row.id;
          that.sysMessage.content = value;
          that.$request.doPost('/admin/sysMessage/sub', that.sysMessage).then(res => {
            if (res.isOk) {
              that.sysMessage.content ="";
              that.showSuccess("");
              that.getList();
            }
          });
        })
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            let ids = that.selectedData.map(function (v) {return v.id});
            that.$request.doGet('/admin/sysMessage/delAll', {ids: ids}).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              }
            });
          } else {
            that.showWarn("");
          }
        });
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.$request.doGet('/admin/sysMessage/delete?id=' + id).then(res => {
            if (res.isOk) {
              that.showSuccess("");
              that.getList();
            }
          });
        });
      }
    }
  };
</script>



