<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="singer" ref="singerForm" :rules="rules" label-width="120px" class="edit-form">
        <el-form-item label="" class="hidden" prop="id">
          <el-input type="hidden" v-model="singer.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="singer" v-model="singer.picture" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="singer.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-select v-model="singer.sex" placeholder="" filterable :disabled="dialog.disabled" class="sui-input">
            <el-option v-for="dict in sexDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="regionArea">
          <el-select v-model="singer.regionArea" placeholder="" filterable :disabled="dialog.disabled" class="sui-input">
            <el-option v-for="dict in regionAreaDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="introduction" class="sui-editor-item">
          <sui-editor v-model="singer.introduction" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "singerForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        singer: {
          picture: "",
          name: "",
          sex: "",
          regionArea: "",
          introduction: "",
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'change'}],
          name: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'singer',tableName:'singer',column:'name',message:'',trigger:'blur'}],
          sex: [{required: true, message: '', trigger: 'change'}],
          regionArea: [{required: true, message: '', trigger: 'change'}],
          introduction: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
      //
      sexDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
      //
      regionAreaDict() {
        return this.$store.getters.getDictArray('region_area');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSinger(option.id);
        }
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.singerForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSinger(id) {
        this.$request.doGet("/admin/singer/get?id=" + id).then(res => {
          if (res.isOk) {
            this.singer = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.singerForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/singer/sub', that.singer).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      }

    }
  }
</script>

<style scoped>

</style>



