<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="notice" ref="noticeForm" :rules="rules" label-width="120px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="notice.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="notice" v-model="notice.picture" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="notice.title" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="noticeType">
          <el-select v-model="notice.noticeType" placeholder="" filterable :disabled="dialog.disabled" class="sui-input">
            <el-option v-for="dict in noticeTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item class="hidden" label="" prop="publishTime">
          <el-date-picker v-model="notice.publishTime" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd" class="sui-input"/>
        </el-form-item>
        <el-form-item v-if="pageFrom=='view'" label="" prop="content">
          <div class="content" v-html="notice.content"></div>
        </el-form-item>
        <el-form-item v-else label="" prop="content" class="sui-editor-item">
          <sui-editor v-model="notice.content" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "noticeForm",
    data() {
      return {
        pageFrom:"",
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        notice: {
          picture: "",
          title: "",
          noticeType: "",
          publishTime: "",
          content: ""
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'change'}],
          title: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'notice',tableName:'notice',column:'title',message:'',trigger:'blur'}],
          noticeType: [{required: true, message: '', trigger: 'change'}],
          content: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
      //
      noticeTypeDict() {
        return this.$store.getters.getDictArray('notice_type');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        this.pageFrom = option.pageFrom
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getNotice(option.id);
        }
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.noticeForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getNotice(id) {
        this.$request.doGet("/admin/notice/get?id=" + id).then(res => {
          if (res.isOk) {
            this.notice = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.noticeForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/notice/sub', that.notice).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      }
    }
  }
</script>

<style scoped>
  .content {
    background-color: #F5F7FA;
    border: solid 1px #dfe4ed;
    color: #C0C4CC;
    border-radius: 10px;
    white-space: pre-wrap;
    padding: 0 10px;
    margin-right: 10px;
  }
</style>



