<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="songSheet" ref="songSheetForm" :rules="rules" label-width="120px" class="edit-form">
        <el-form-item label="" class="hidden" prop="id">
          <el-input type="hidden" v-model="songSheet.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="songSheet" v-model="songSheet.picture" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="songSheet.title" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="categoryId">
          <el-select v-model="songSheet.categoryId" placeholder="" filterable :disabled="dialog.disabled" class="sui-input">
            <el-option v-for="category in categoryDict" :key="category.id" :value="category.id" :label="category.name"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="introduction" class="sui-editor-item">
          <sui-editor v-model="songSheet.introduction" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "songSheetForm",
    data() {
      return {
        categoryDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        songSheet: {
          picture: "",
          title: "",
          categoryId: "",
          introduction: "",
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'change'}],
          title: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'songSheet',tableName:'song_sheet',column:'title',message:'',trigger:'blur'}],
          categoryId: [{required: true, message: '', trigger: 'change'}],
          introduction: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
    },
    methods: {
      //
      getCategoryDict() {
        this.$request.doGet('/admin/category/getList').then(res => {
          if (res.isOk) {
            this.categoryDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getCategoryDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSongSheet(option.id);
        }
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.songSheetForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSongSheet(id) {
        this.$request.doGet("/admin/songSheet/get?id=" + id).then(res => {
          if (res.isOk) {
            this.songSheet = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.songSheetForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/songSheet/sub', that.songSheet).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      }

    }
  }
</script>

<style scoped>

</style>



