<!--  -->
<template>
  <div class="body">
    <el-dialog class="register-dialog" :title="dialog.title" :close-on-click-modal="false" center style="margin-top: 200px;" width="500px" :visible.sync="dialog.open">
      <el-form style="margin-left: 50px" :model="sysUser" ref="registerForm" :rules="rules" label-width="100px">
        <el-form-item label="" prop="loginName">
          <el-input v-model="sysUser.loginName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="sysUser.password" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="prePassword">
          <el-input v-model="sysUser.prePassword" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitRegister('registerForm')"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>

  export default {
    name: "registerForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysUser: {
          photo: "",
          loginName: "",
          prePassword:"",
          password: ""
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          prePassword: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    methods: {
      //
      open(option) {
        this.showDialog(option.title, false);
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitRegister: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            if (that.sysUser.password != that.sysUser.prePassword) {
              this.showWarn("!")
              return false
            }
            that.$request.doPost('/admin/login/register', that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.registerForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped lang="scss">
  @import '../../../../assets/styles/theme';
  .register-dialog /deep/ .el-dialog__headerbtn .el-dialog__close{
    color: white!important;
  }
  .register-dialog /deep/ .el-dialog__header  {
    background: #409EFF;
  }
  .register-dialog /deep/ .el-dialog__title  {
    font-weight: bold;
    color: white!important;
  }
</style>



