<!--  -->
<template>
  <div class="body">
    <el-dialog width="800px" :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form style="height: auto" :model="sysOffice" ref="sysOfficeForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="" class="hidden" prop="id">
          <el-input v-model="sysOffice.id" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item v-if="sysOffice.pid!='0'" label="" prop="pid">
          <sui-office v-model="sysOffice.pid" type="0" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="sysOffice.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item v-if="sysOffice.pid!='0'" label="" prop="type">
          <el-select v-model="sysOffice.type" placeholder="" :disabled="dialog.disabled" class="sui-input">
            <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item v-if="sysOffice.pid!='0'" label="" prop="code">
          <el-input v-model="sysOffice.code" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="leader">
          <el-input v-model="sysOffice.leader" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="sysOffice.email" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="sysOffice.phone" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input type="textarea" v-model="sysOffice.remarks" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysOfficeForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "sysOfficeForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysOffice: {
          pid: "",
          name: "",
          code: "",
          type: "",
          leader: "",
          email: "",
          phone: "",
          sort: "",
          remarks: ""
        },
        //
        rules: {
          pid: [{required: true, message: '', trigger: 'blur'}],
          name: [{required: true, message: '', trigger: 'blur'},{validator: this.$validator.unique, form: this, tableName: 'sys_office', column: 'name', model: 'sysOffice', message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'blur'}],
          code: [{required: true, message: '', trigger: 'blur'},{validator: this.$validator.unique, form: this, tableName: 'sys_office', column: 'code', model: 'sysOffice', message: '', trigger: 'blur'}],
          email: [{type: 'email', message: '', trigger:'blur'},{validator:this.$validator.unique,form:this,model:'sysOffice',tableName:'sys_office',column:'email',message: '',trigger: 'blur'}],
          phone: [{validator: this.$validator.phone, message: '11', trigger: 'blur'},{validator: this.$validator.unique, form: this,model: 'sysOffice', tableName: 'sys_office', column: 'phone',  message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
      //
      typeDict() {
        return this.$store.getters.getDictArray('office_type');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.sysOffice.pid=option.id;
        this.sysOffice.name = "";
        this.sysOffice.code = "";
        if (pageFrom == 'edit' || pageFrom == 'view') {
          //
          this.getSysOffice(option.id)
        }
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //id
      getSysOffice(id){
        this.$request.doGet("/admin/sysOffice/get?id="+id).then(res => {
          if (res.isOk) {
            this.sysOffice = res.obj;
          }
        });
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/sysOffice/sub', that.sysOffice).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysOfficeForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>
</style>



