<!--  -->
<template>
  <div class="body">
    <el-dialog width="600px" :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="sysUser.id"/>
        </el-form-item>
        <el-form-item label="" prop="loginName">
          <el-input v-model="sysUser.loginName" placeholder="" :disabled="dialog.disabled" class="sui-input"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userName">
          <el-input v-model="sysUser.userName" placeholder="" :disabled="dialog.disabled" class="sui-input"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-select v-model="sysUser.sex" placeholder="" :disabled="dialog.disabled" class="sui-input">
            <el-option v-for="dict in sexDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="sysUser.phone" placeholder="" :disabled="dialog.disabled" class="sui-input"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="sysUser.email" placeholder="" :disabled="dialog.disabled" class="sui-input"></el-input>
        </el-form-item>
        <el-form-item label="" v-if="!sysUser.id" prop="roleList" >
          <el-radio-group v-model="sysUser.roleList[0]">
            <el-radio v-for="role in allRole" :key="role.roleCode" :label="role" border :disabled="!showRole">{{role.roleName}}</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysUserForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    components:{
    },
    name: "userForm",
    data() {
      return {
        showRole:false,
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        allRole:[],
        //
        sysUser: {
          roleList:[{roleCode:'userRole'}],//
          photo: "",
          loginName: "",
          password: "",
          userName: "",
          office:{},
          userNo: "",
          email: "",
          phone: "",
          officeId: ""
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'},{validator: this.$validator.unique, form: this, model: 'sysUser', tableName: 'sys_user', column: 'login_name', message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          userName: [{required: true, message: '', trigger: 'blur'}],
          sex: [{required: true, message: '', trigger: 'blur'}],
          userNo: [{required: true, message: '', trigger: 'blur'}],
          email: [{type: 'email', message: '', trigger: ['blur', 'change']},{validator: this.$validator.unique, form: this,model: 'sysUser', tableName: 'sys_user', column: 'email',  message: '', trigger: 'blur'}],
          phone: [{validator: this.$validator.phone, message: '11', trigger: 'blur'},
                 {validator: this.$validator.unique, form: this,model: 'sysUser', tableName: 'sys_user', column: 'phone',  message: '', trigger: 'blur'}],
          roleList: [{required: true, message: '', trigger: 'blur'}],
          officeId: [{required: true, message: '', trigger: ['blur', 'change']}]
        }
      }
    },
    computed: {
      sexDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
    },
    methods: {
      //
      open(option) {
        const that = this;
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        that.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.showRole = false
          that.getUser(option.id);
        }else{
          that.getRoleList();//
          that.showRole = true
        }
      },
      //
      getRoleList(userId){
        const that = this;
        that.$request.doGet('/admin/sysUser/findCheckedRolesByUserId?userId=' + userId).then(res => {
          if (res.isOk) {
            that.allRole = res.list.filter(function (v) {
              return v.roleCode != 'adminRole'
            });
            that.sysUser.roleList = that.allRole.filter(function (v) {
              return v.checked
            })
          }
        });
      },
      //id
      getUser(id) {
        const that = this;
        this.$request.doGet("/admin/sysUser/get?id="+id).then(res => {
          if (res.isOk) {
            that.sysUser = res.obj;
            that.getRoleList(id);//
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            // that.sysUser.roleList = new Array(that.sysUser.roleList);
            that.$request.doPost('/admin/sysUser/sub', that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
  [v-cloak]{
    display: none;
  }
</style>



