<!--  -->
<template>
  <div class="body">
    <!--  -->
    <sysDict-form ref="sysDictDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="label">
        <el-input v-model="queryForm.label" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="value">
        <el-input v-model="queryForm.value" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-input v-model="queryForm.type" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['sysDict:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysDict:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table  row-key="id" :tree-props="{children: 'children', hasChildren: 'hasChildren'}" border :header-cell-style="{background:'#f4f4f5'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" prop="label" sortable>
        <template style="color: green" slot-scope="scope">
          <span v-if="scope.row.sort<=100" style="color: #409EFF">{{scope.row.label}}</span>
          <span v-else>{{scope.row.label}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="value" sortable/>
      <el-table-column label="" prop="type" sortable/>
<!--      <el-table-column label="" prop="style" sortable/>-->
      <el-table-column label="" prop="sort" sortable/>
<!--      <el-table-column label="" prop="remarks" sortable/>-->
      <el-table-column label="" align="left"  width="380" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysDict:view']"></el-button>
          <el-button size="mini" type="danger" :disabled="scope.row.sort<=100" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysDict:delete']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysDict:edit']"></el-button>
          <el-button size="mini" type="primary" v-if="scope.row.type=='dict'" plain icon="el-icon-document-add" @click="openDialog({title:'',pageFrom:'dictCode',id:scope.row.id})" v-hasPermi="['sysDict:edit']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysDictForm from './dictForm'
  export default {
    components: {sysDictForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        total: 0, //
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          pid: "",
          label: "",
          value: "",
          type: "",
          style: "",
          sort: "",
          remarks: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
    },
    computed: {
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doGet('/admin/sysDict/getTreePage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        });
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
        this.handleQuery();
      },
      //
      openDialog(option) {
        this.$refs.sysDictDialog.open(option);
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;cc
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            let ids = that.selectedData.map(function (v) {return v.id});
            that.$request.doGet('/admin/sysDict/delAll', {ids: ids}).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              }
            });
          } else {
            that.showWarn("");
          }
        });
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.$request.doGet('/admin/sysDict/delete?id=' + id).then(res => {
            if (res.isOk) {
              that.showSuccess("");
              that.getList();
            }
          });
        });
      }
    }
  };
</script>



