<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="song" ref="songForm" :rules="rules" label-width="120px" class="edit-form">
        <el-form-item label="" class="hidden" prop="id">
          <el-input type="hidden" v-model="song.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="song" v-model="song.picture" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="song.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="singerId">
          <el-select v-model="song.singerId" placeholder="" filterable :disabled="dialog.disabled" class="sui-input">
            <el-option v-for="singer in singerDict" :key="singer.id" :value="singer.id" :label="singer.name"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="lyric">
          <el-input type="textarea" v-model="song.lyric" placeholder="" :disabled="dialog.disabled" class="sui-textarea"/>
        </el-form-item>
        <el-form-item label="" prop="introduction" class="sui-editor-item">
          <sui-editor v-model="song.introduction" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="fileList">
          <sui-file type="song" :limit="1" :refId="song.id" v-model="song.fileList" v-if="dialog.open" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "songForm",
    data() {
      return {
        singerDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        song: {
          picture: "",
          name: "",
          singerId: "",
          lyric: "",
          introduction: "",
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'change'}],
          name: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'song',tableName:'song',column:'name',message:'',trigger:'blur'}],
          singerId: [{required: true, message: '', trigger: 'change'}],
          lyric: [{required: true, message: '', trigger: 'blur'}],
          introduction: [{required: true, message: '', trigger: 'blur'}],
          fileList: [{required: true,type:'array', message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
    },
    methods: {
      //
      getSingerDict() {
        this.$request.doGet('/admin/singer/getList').then(res => {
          if (res.isOk) {
            this.singerDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getSingerDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSong(option.id);
        }
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.songForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSong(id) {
        this.$request.doGet("/admin/song/get?id=" + id).then(res => {
          if (res.isOk) {
            this.song = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.songForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/song/sub', that.song).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      }

    }
  }
</script>

<style scoped>

</style>



