<!--  -->
<template>
  <div class="body">
    <el-dialog width="600px" :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
        <el-form-item label="" prop="loginName">
          <sui-photo type="user" v-model="sysUser.photo" :disabled="dialog.disabled" :url="sysUser.photo"></sui-photo>
        </el-form-item>
        <el-form-item label="" prop="loginName">
          <el-input v-model="sysUser.loginName"  placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userName">
          <el-input v-model="sysUser.userName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="sysUser.phone" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="sysUser.email" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {mapActions} from "vuex";
  export default {
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysUser: {
          photo: "",
          loginName: "",
          userName: "",
          userNo: "",
          email: "",
          phone: "",
          officeId: ""
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          userName: [{required: true, message: '', trigger: 'blur'}],
          email: [{type: 'email', message: '', trigger:'blur'},{validator:this.$validator.unique,form:this,model:'sysUser',tableName:'sys_user',column:'email',message: '',trigger: 'blur'}],
          phone: [{validator: this.$validator.phone, message: '11', trigger: 'blur'},{validator: this.$validator.unique, form: this,model: 'sysUser', tableName: 'sys_user', column: 'phone',  message: '', trigger: 'blur'}],
        }
      }
    },
    computed:{
      userInfo(){
        return this.$store.getters.userInfo;
      }
    },
    methods: {
      ...mapActions(['commitUpdateUserInfo']),
      //
      open(option) {
        const that = this;
        const user = JSON.parse(JSON.stringify(this.userInfo))
        that.sysUser = user;
        that.showDialog(option.title, false);
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.sysUserForm.validate((valid) => {
          if (valid) {//
            that.commitUpdateUserInfo(that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reload');
              }
            })
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



