<!--  -->
<template>
  <div class="body">
    <el-dialog width="800px" :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form style="height: auto" :model="category" ref="categoryForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="category.id"/>
        </el-form-item>
        <el-form-item  label="" prop="name">
          <el-input v-model="category.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item  label="" prop="content">
          <el-input type="textarea" v-model="category.content" placeholder="" :disabled="dialog.disabled" class="sui-textarea"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "categoryForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        category: {
          name: "",
          content: ""
        },
        //
        rules: {
          name: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'category',tableName:'category',column:'name',message:'',trigger:'blur'}],
          content: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getCategory(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getCategory(id) {
        this.$request.doGet("/admin/category/get?id=" + id).then(res => {
          if (res.isOk) {
            this.category = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.categoryForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/category/sub', that.category).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.categoryForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



