<!--  -->
<template>
  <div class="body">
    <!--  -->
    <sysColumn-form ref="sysColumnDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="tableName">
        <el-input v-model="queryForm.tableName" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="tableDesc">
        <el-input v-model="queryForm.tableDesc" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="columnName">
        <el-input v-model="queryForm.columnName" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="columnDesc">
        <el-input v-model="queryForm.columnDesc" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['sysColumn:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysColumn:delete']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-export type="primary" size="mini" action="/admin/sysColumn/export" title="" v-hasPermi="['sysColumn:export']"/>
      </el-col>
      <el-col :span="1.5">
        <el-import type="primary" size="mini" action="/admin/sysColumn/import" title="" v-hasPermi="['sysColumn:import']"/>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#f4f4f5'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="tableName" sortable/>
      <el-table-column label="" prop="tableDesc" sortable/>
      <el-table-column label="" prop="columnName" sortable/>
      <el-table-column label="" prop="columnDesc" sortable/>
      <el-table-column label="" prop="inputType" sortable/>
      <el-table-column label="" prop="queryType" sortable/>
      <el-table-column label="" prop="dictType" sortable/>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysColumn:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysColumn:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysColumn:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysColumnForm from './columnForm'
    export default {
      components: {sysColumnForm},
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            tableName: "",
            tableDesc: "",
            columnName: "",
            columnDesc: "",
            inputType: "",
            queryType: "",
            dictType: "",
            associateType: "",
            isRequired: "",
            isUnique: "",
            isPk: "",
            isShow: "",
            sort: "",
            remarks: ""
          }
        };
      },
      mounted() {
        //
        this.getList();
      },
      computed: {
        //
        isRequiredDict() {
          return this.$store.getters.getDictArray('sys_yes_no');
        },
        //
        isUniqueDict() {
          return this.$store.getters.getDictArray('sys_yes_no');
        },
        //
        isPkDict() {
          return this.$store.getters.getDictArray('sys_yes_no');
        },
        //
        isShowDict() {
          return this.$store.getters.getDictArray('sys_yes_no');
        },
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doGet('/admin/sysColumn/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
          this.handleQuery();
        },
        //
        openDialog(option) {
          this.$refs.sysColumnDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              let ids = that.selectedData.map(function (v) {return v.id});
              that.$request.doGet('/admin/sysColumn/delAll', {ids: ids}).then(res => {
                if (res.isOk) {
                  that.showSuccess("");
                  that.getList();
                }
              });
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.$request.doGet('/admin/sysColumn/delete?id=' + id).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              }
            });
          });
        }
      }
    };
</script>



